/**
 * @fileoverview This file contains server-side actions for the application.
 * Server actions are functions that run on the server and can be called directly
 * from client components, simplifying client-server communication.
 */
'use server';

import { query } from '@/lib/db';

/**
 * Authenticates a user against the database based on their identifier and password.
 * @param {string} identifier - The user's employee ID or email.
 * @param {string} password - The user's password.
 * @returns {Promise<{success: boolean, message: string, user?: object, redirectUrl?: string}>}
 *          An object indicating the result of the authentication attempt.
 */
export async function authenticateUser(identifier, password) {
  try {
    if (!identifier || !password) {
      return { success: false, message: 'Identifier and password are required.' };
    }

    // Check if the identifier is an email (for HR or Admin)
    if (identifier.includes('@')) {
      // Check HR personnel first
      const hrResult = await query('SELECT * FROM hr_personnel WHERE email = $1', [identifier]);
      if (hrResult.rows.length > 0) {
        const user = hrResult.rows[0];
        if (password === user.password) {
          return {
            success: true,
            message: 'Login successful',
            user: { 
              id: user.id, 
              name: `${user.first_name} ${user.last_name}`, 
              email: user.email, 
              role: 'hr' 
            },
            redirectUrl: '/hr/dashboard'
          };
        } else {
          return { success: false, message: 'Invalid credentials.' };
        }
      }

      // If not in HR, check Admins
      const adminResult = await query('SELECT * FROM admins WHERE email = $1', [identifier]);
      if (adminResult.rows.length > 0) {
        const user = adminResult.rows[0];
        if (password === user.password) {
          return {
            success: true,
            message: 'Login successful',
            user: { 
              id: user.id, 
              name: `${user.first_name} ${user.last_name}`, 
              email: user.email, 
              role: 'admin' 
            },
            redirectUrl: '/admin/dashboard'
          };
        } else {
          return { success: false, message: 'Invalid credentials.' };
        }
      }
    } else {
      // If not an email, assume it's an employee ID
      const employeeResult = await query("SELECT * FROM employees WHERE id = $1 AND status = 'Active'", [identifier]);
      if (employeeResult.rows.length > 0) {
        const user = employeeResult.rows[0];
        if (password === user.password) {
          return {
            success: true,
            message: 'Login successful',
            user: { 
              id: user.id, 
              name: `${user.first_name} ${user.last_name}`,
              email: null, // Employees do not have an email
              role: 'employee' 
            },
            redirectUrl: '/dashboard'
          };
        } else {
          return { success: false, message: 'Invalid credentials.' };
        }
      }
    }

    // If no user was found in any table
    return { success: false, message: 'Invalid credentials.' };

  } catch (error) {
    console.error('Authentication Server Action Error:', error);
    return { success: false, message: 'An internal server error occurred.' };
  }
}
